body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  line-height: 300px;
  text-align: center;

  flex-basis: 33%;
  min-width: 300px;
  flex-grow: 1;

  font-size: 100px;
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
  
  background-color: red;
}

.block-2 {
  background-color: rgb(204, 0, 0);
}

.block-3 {
  background-color: rgb(153, 0, 0);
}

.block-4 {
  background-color: rgb(102, 0, 0);
}

.block-5 {
  background-color: rgb(51, 0, 0);
}

.block-6 {
  background-color: black;
}

@media (min-width: 600px) {
  .block-1 {
    order: 1;
  }

  .block-2 {
    order: 2;
  }

  .block-3 {
    order: 4;
  }

  .block-4 {
    order: 3;
  }

  .block-5 {
    order: 5;
  }

  .block-6 {
    order: 6;
  }
}

@media (min-width: 900px) {
  .block-1 {
    order: 1;
  }

  .block-2 {
    order: 2;
  }

  .block-3 {
    order: 3;
  }

  .block-4 {
    order: 6;
  }

  .block-5 {
    order: 5;
  }

  .block-6 {
    order: 4;
  }
}
